.aside_alert__overlay {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: none;
  opacity: 0;
  transition: opacity 0.5;
  z-index: 10;
}
.aside_alert__overlay--visible {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  opacity: 1;
  justify-content: center;
  transition: opacity 0.5 ease-in;
  z-index: 10;
}
.aside_alert__popupbox {
  position: relative;
  max-width: 920px;
  min-width: 190px;
  height: 210px;
  background-color: #FFFFFF;
  z-index: 11;
  display: none;
  opacity: 0;
  transition: opacity 0.5;
  top: 40vh;
  gap: 317px;
}
.aside_alert__popupbox--visible {
  position: relative;
  max-width: 920px;
  min-width: 190px;
  height: 210px;
  background-color: #FFFFFF;
  z-index: 11;
  opacity: 1;
